.toggelDiv {
  display: none;
  height: 2.5rem;
  width: 2.5rem;
  margin: 0.3rem 0.8rem;
  background-color: #cdc8c8;
  border-radius: 50%;
  cursor: pointer;

  @media screen and (max-width: 768px) {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999;
  }
}

.asideNavbar__panel {
  height: 100vh;
  min-width: 200px;
  border-right: 0.5px solid rgb(230, 227, 227);
  background-color: white;

  .top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;

    .logo {
      font-size: 25px;
      font-weight: bold;
      color: #27aa5d;
    }
  }

  hr {
    height: 0;
    border: 0.5px solid rgb(230, 227, 227);
  }

  .center {
    padding-left: 10px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      .title {
        font-size: 10px;
        font-weight: bold;
        color: #999;
        margin-top: 15px;
        margin-bottom: 5px;
      }

      li {
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover {
          background-color: #ece8ff;
        }

        .icon {
          font-size: 18px;
          color: #2cd498;
        }

        span {
          font-size: 16px;
          font-weight: 600;
          color: #888;
          margin-left: 10px;
        }
      }
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    margin: 10px;

    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;

      &:nth-child(1) {
        background-color: whitesmoke;
      }

      &:nth-child(2) {
        background-color: #333;
      }

      &:nth-child(3) {
        background-color: darkblue;
      }
    }
  }

  @media screen and (max-width: 768px) {
    display: none;
  }
}

.asideNavbar__menu {
  height: 100vh;
  min-width: 230px;
  background-color: rgb(224 221 239 / 75%);
  backdrop-filter: blur(20px);
  position: absolute;
  left: -20rem;
  z-index: 99999999;
  transition: all 0.5s ease-in-out;

  .toggelDiv {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 2.5rem;
    width: 2.5rem;
    padding: 10px 10px;
    background-color: darkgray;
    border-radius: 50%;
    cursor: pointer;
  }

  .top {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .logo {
      font-size: 25px;
      font-weight: bold;
      color: #2cd498;
    }
  }

  hr {
    height: 0;
    border: 0.5px solid rgb(230, 227, 227);
  }

  .center {
    padding-left: 10px;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      .title {
        font-size: 15px;
        font-weight: bold;
        color: rgb(101, 101, 101);
        margin-top: 15px;
        margin-bottom: 5px;
      }

      li {
        display: flex;
        align-items: center;
        padding: 5px;
        cursor: pointer;

        &:hover {
          background-color: #d3cfe7;
        }

        .icon {
          font-size: 20px;
          color: #2cd498;
        }

        span {
          font-size: 18px;
          font-weight: 600;
          color: rgb(101, 101, 101);
          margin-left: 10px;
        }
      }
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    margin: 10px;

    .colorOption {
      width: 20px;
      height: 20px;
      border-radius: 5px;
      border: 1px solid #7451f8;
      cursor: pointer;
      margin: 5px;

      &:nth-child(1) {
        background-color: whitesmoke;
      }

      &:nth-child(2) {
        background-color: #333;
      }

      &:nth-child(3) {
        background-color: darkblue;
      }
    }
  }
}
