.products {
	flex-grow: 1;
	background-color: #fafafa;
	height: calc(100vh - 50px);

	.products-scroll {
		overflow: scroll;
		height: 100%;
	}

	.product-header {
		display: flex;
		justify-content: space-between;
		margin: 1rem;
	}

	.title {
		color: #888;
		font-size: x-large;
		font-weight: bold;
		margin-left: 0.5rem;
		margin-right: 0.5rem;
	}

	.card {
		background-color: white;
		transition: 0.3s;
		margin: 20px;
		border: 2px rgba(180, 180, 180, 0.329) solid;
		border-radius: 5px;
	}

	.container {
		box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
		padding: 2px 2px;
		height: 100%;
	}

	.btn {
		border: 2px solid black;
		background-color: white;
		color: black;
		padding: 5px 10px;
		font-size: 16px;
		font-weight: bold;
		border-radius: 0.3rem;
		cursor: pointer;
		transition: color 0.4s, background-color 0.4s;
	}

	.btn:disabled {
		opacity: 0.5;
		cursor: not-allowed;
	}

	/* Green */
	.success {
		border-color: #04AA6D;
		color: #04AA6D;
	}

	.success:hover {
		background-color: #04AA6D;
		color: white;
	}

	/* Blue */
	.info {
		border-color: #2196F3;
		color: dodgerblue;
	}

	.info:hover {
		background: #2196F3;
		color: white;
	}

	/* Orange */
	.warning {
		border-color: #ff9800;
		color: orange;
	}

	.warning:hover {
		background: #ff9800;
		color: white;
	}

	/* Red */
	.danger {
		border-color: #f44336;
		color: rgb(222, 72, 72);
	}

	.danger:hover {
		background: #f44336;
		color: white;
	}

	.default {
		border-color: #e7e7e7;
		color: black;
	}

	.default:hover {
		background: #e7e7e7;
	}

	@media screen and (max-width: 768px) {
		height: 110vh;
		width: 100vw;
	}
}



.my_modal_container {
	@media screen and (max-width: 768px) {
		flex-direction: column-reverse;
	}
}

.my_card {
	-webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
	background-color: white;
	transition: 0.3s;
	margin: 10px;
	border: 2px rgba(180, 180, 180, 0.329) solid;
	border-radius: 5px;

	.my_form_control {
		width: 100%;
		padding: 2px 5px;
		border: none;
		border-bottom: 1px solid gray;

		&:focus {
			outline: none;
			border-width: 2px;
		}
	}
}

.my_btn {
	&:hover {
		box-shadow: 0px 0px 6px 4px #e0e0e0;
	}
}